@charset "utf-8";
.wrap_login h2{
    text-align: center;
}

.inner1{
height: 500px;
    position: relative;
    top:50px;
    width: 356px;
    margin: 0px auto;   
}
.login{
position: absolute;
top: 100px;
width: 350px;
height: 45px;
background-color: #fff;
color:#333;
border: 1px solid rgb(78, 78, 78);
display: block;
text-indent: 7px;
}
.pw{
text-indent: 7px;
position: absolute;
top: 153px;
width: 350px;
height: 45px;
background-color: #fff;
color:#333;
border: 1px solid rgb(78, 78, 78);
display: block;
}
.pw:focus{
color: #333;
}

.log_btn{
position: absolute;
top: 230px;
left: 50%;
transform: translateX(-50%);
width: 360px;
height: 55px;
background-color: #5f0080;
color: #fff;
border: 1px solid #5f0080;
font-size: 20px;
display: block;


}
.wrap_login{
width: 100%;

z-index: -20;


}
.more1{
position: relative;
top: 300px;

font-size: 13px;

}

.more1 a{
text-decoration: none;
color: #333;
}
.more1 a::after{
content:"";
width: 1px;
height: 13px;
background-color: #333;
color: #333;
position: absolute;
top: 1px;


}
.more1 a:last-child::after{
display: none;
}

.wrap_login p {
margin: 0px auto;
text-align: center;
transform: translateY(360px);
color: #333;
font-size: 18px;
font-weight: bold;
}


.wrap_login p::after{
content: "";
width: 130px;
height: 2px;
background-color: #333;
position: absolute;
top: 12px;
right: -40px;
}
.wrap_login p::before{
content: "";
width: 130px;
height: 2px;
background-color: #333;
position: absolute;
top: 12px;
left: -40px;
}
