footer{

}
.f_inner{
    width: 1050px;
    height: 448px;
    margin: 0px auto;
    position: relative;
}
.left{
    position: absolute;
    left: 0px;
}
.p_num{
    margin: 20px 0px;
font-size: 16px;
word-spacing: -2px;
}
.p_num span{
    font-size: 30px;
    font-weight: bold;
    word-spacing: 1px;margin-right: 8px;
}

.btn_wrap{
display: flex;
margin: 18px 0px;
}
.btn_wrap input{
    width: 140px;
    height: 40px;
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 3px;
    margin-right: 20px;
}
.btn_wrap p{
    font-size: 14px;
    color: rgb(128, 128, 128);
}
.email{
    color: rgb(145, 145, 145);
    font-size: 12px;
}
.right{
    position: absolute;
    right: 0px;
}
.right .service{
    display: flex;
    font-size: 14px;
    margin-bottom: 30px;
    
}
.right .service li{
    margin-right: 11px;
}
.service_txt{
    font-size: 12px;
    color: rgb(141, 141, 141);
    line-height: 19px;
}
.sns{margin-top: 40px;
    display: flex ;
}
.sns li{
    margin: 0px 5px;
}
.sns li img{
    width: 30px;
}
.confirm{width: 100%;
position: absolute;
bottom: 0px;
}
.confirm ul{
    display: flex;
    justify-content:space-between;
}
.confirm ul li img{
    height: 32px;
    margin-right: 8px;
}
.confirm ul li{
display: flex;
justify-content:space-between;
margin: 10px 0px 30px 0px;
}
.confirm p{
    font-size: 10px;
    color: rgb(117, 117, 117);
}
.address{
    height: 80px;
    padding: 20px 0px 30px;
    font-size: 10px;
    line-height: 15px;
    text-align: center;
    background-color: rgb(247, 247, 247);
    color: rgb(153, 153, 153);
}