*{
    margin: 0px;
    padding: 0px;
}

header{
width: 100%;
}
li{
    list-style: none;
}
header .inner{
    width: 1050px;
    height: 156px;
    margin: 0px auto;
    position: relative;

}
.inner .sign{
    width: 100%;
    height: 40px;
    position: absolute ;
  top: 0px;
  line-height: 35px;

}
.inner .sign ul{
 position: absolute;
 right:0px;
    display: flex;
    font-size: 12px;
}
.inner .sign ul li{
    margin: 0px 5px;
}
.sign ul li::after{
content: "";
width: 1px;
height: 12px;
background-color: #ccc;
position: absolute;
top: 10px; 
}
.inner .sign ul li:last-child::after{
    display: none;
}
.search_blank{
    line-height: 64px;
    width: 100%;
    height: 64px;
position: absolute;
top: 40px;
display: flex;
}

.search_blank h1 {
    display: inline-block;
}
.search_blank h1 img{
    width: 82px;
    margin-right: 20px;
}
.kully_option li::after{
content: "";
width: 1px;
height: 18px;
background-color: #ccc;
position: absolute;
top: 15px;
}
.kully_option li:last-child:after{
    display: none;
}
.search_blank ul{ display: flex; margin-right: 50px;}
.search_blank ul li:first-child{color: #5f0080;}
.search_blank ul li{font-size:18px;font-weight: bold;
color: #ccc;
height: 50px;
line-height: 50px;}

.search_blank input{
    background-color: #fff;
    border: 1px solid #5f0080;
    border-radius: 5px;
    width: 410px;
    height: 48px;
    font-size: 16px;
    text-indent: 10px;


}
.search_blank label img{
    position: relative;
    right: 38px;
}
.imoge{
    width: 150px;
    position: absolute;
    right: 00px;
}
.imoge ul li{
    margin: 0px 8px;
}
.category_blank{
    position: absolute;
    bottom: 0px;
    width: 100%;
    height: 56px;
    border-bottom: 1px solid #ccc;
    display: flex;
    line-height: 56px;
}
.category{
    margin-right: 180px;
}
.category_option{
display: flex;
}
.category_option li{
    width: 150px;
}
.category_blank .btn{
    position: absolute;
    right: 4px;
}
.category_blank .btn a{
    text-decoration: none;
    font-size: 12px;
width: 100px;
padding: 6px 16px;
border-radius: 20px;
background-color: #fff;
border: 1px solid #ccc;
font-weight: bold;
}


a{text-decoration: none;color: #333;}